import React from "react";

import "./add-person.css";



const AddPerson = ( { onClose, onPersonAdded } ) => {
   const serviceFunction = ( event ) => {
       event.preventDefault();
       const fields = document.forms.addPerson;

       onPersonAdded( fields );


   }
    return (
        <form className="add-person" name="addPerson" onSubmit={ serviceFunction }>
            <button
                onClick={ () => onClose() }
                type="button" className="close">
                <span aria-hidden="true">&times;</span>
            </button>
            <div className="form-group">
                <label htmlFor="exampleFormControlInput1">ID</label>
                <input name="id"
                    type="text" className="form-control" id="exampleFormControlInput1"
                       placeholder="ID" />
            </div>

            <div className="form-group">
                <label htmlFor="exampleFormControlInput1">First Name</label>
                <input name="firstName"
                    type="text" className="form-control" id="exampleFormControlInput1"
                       placeholder="First Name" />
            </div>

            <div className="form-group">
                <label htmlFor="exampleFormControlInput1">Last Name</label>
                <input name="lastName"
                    type="text" className="form-control" id="exampleFormControlInput1"
                       placeholder="Last Name" />
            </div>

            <div className="form-group">
                <label htmlFor="exampleFormControlInput1">Email address</label>
                <input name="email"
                    type="email" className="form-control" id="exampleFormControlInput1"
                       placeholder="Email" />
            </div>

            <div className="form-group">
                <label htmlFor="exampleFormControlInput1">Phone</label>
                <input name="phone"
                    type="text" className="form-control" id="exampleFormControlInput1"
                       placeholder="Phone" />
            </div>

            <button type="submit" className="btn btn-success">Add</button>
        </form>
    )
}

export default AddPerson;